<template>
  <div class="game-history-widget">
    <div class="game-history-widget__section">
      <div class="game-history-widget__wrap">
        <table v-if="gameHistory.length">
          <thead>
            <tr>
              <th>Player</th>
              <th>Score</th>
              <th>Game Id</th>
              <th>Date</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(game, index) in gameHistory" :key="index" :class="{'highlight': index === lastAddedIndex, 'game-history-widget__table-row-im': userTag == game.userTag}">
              <td><router-link :to="`/player/${game.userTag}`">{{ game.userTag }}</router-link></td>
              <td>{{ game.score }}₿</td>
              <td><router-link :to="`/games/${game.gameId}`">{{ game.gameId }}</router-link></td>
              <td>{{ formatDate(game.timestamp) }}</td>
            </tr>
          </tbody>
        </table>
        <p v-else>No game history available</p>
      </div>
      
    </div>
    <span v-if="!userTag" class="game-history-widget__description">Only authorized players are shown in the game history. Visit <router-link to="/my">My Bitcat Page</router-link> to get started!</span>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import store from '@/vuex';

export default {
  name: 'GameHistoryWidget',
  computed: {
    ...mapGetters(['gameHistory']),
    userTag() {
      return store.getters.userTag;
    }
  },
  data() {
    return {
      lastAddedIndex: null,
    };
  },
  watch: {
    gameHistory: {
      handler(newHistory) {
        if (newHistory.length) {
          this.lastAddedIndex = 0;
          setTimeout(() => {
            this.lastAddedIndex = null;
          }, 3000);
        }
      },
    },
  },
  methods: {
    ...mapActions(['fetchGameHistory']),
    formatDate(timestamp) {
      return new Date(timestamp).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  },
  created() {
    this.fetchGameHistory();
  }
}
</script>

