<template>
  <div class="home-page">
    <div class="game-container" ref="gameArea" @click="catchCoin">
      <img :src="catImage" :style="catStyle" alt="Cat" />
      <div v-for="(coin, index) in coins" :key="index" class="coin" :style="coin.style"></div>
      <div class="info">
        <p>Score: {{ score.toFixed(1) }}</p>
        <p>Lives: {{ lives }}</p>
        <p v-if="isGameOver">Game Over! Final Score: {{ score.toFixed(1) }}</p>
      </div>
      <button v-if="isGameOver" @click="resetGame">Restart</button>
    </div>
    <GameHistoryWidget />
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted, onUnmounted } from 'vue';
import GameHistoryWidget from '@/components/GameHistoryWidget.vue';

export default {
  components: {
    GameHistoryWidget
  },
  setup() {
    const catImage = ""; // Замените на путь к картинке кота
    const catSize = ref(50);
    const score = ref(0);
    const lives = ref(9);
    const coins = reactive([]);
    const isGameOver = computed(() => lives.value <= 0 || elapsedTime.value >= 30);
    const elapsedTime = ref(0);
    let gameInterval = null;
    let coinInterval = null;

    const gameArea = ref(null);
    const catStyle = computed(() => ({
      width: `${catSize.value}px`,
      height: `${catSize.value}px`,
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      transition: 'all 0.5s ease'
    }));

    const addCoin = () => {
      const gameRect = gameArea.value.getBoundingClientRect();
      const coin = {
        x: Math.random() * gameRect.width,
        y: Math.random() * gameRect.height,
        style: {
          position: 'absolute',
          left: `${Math.random() * (gameRect.width - 20)}px`,
          top: `${Math.random() * (gameRect.height - 20)}px`,
          width: '50px',
          height: '50px',
          backgroundColor: 'gold',
          borderRadius: '50%',
        },
      };
      coins.push(coin);
      setTimeout(() => {
        const index = coins.indexOf(coin);
        if (index > -1) {
          coins.splice(index, 1);
          if(score.value != 0) {
            score.value -= 0.5; 
          }
        }
      }, 2000);
    };

    const catchCoin = (event) => {
      const clickedCoinIndex = coins.findIndex((coin) => {
        const rect = gameArea.value.getBoundingClientRect();
        const coinX = rect.left + parseFloat(coin.style.left);
        const coinY = rect.top + parseFloat(coin.style.top);
        return (
          event.clientX >= coinX &&
          event.clientX <= coinX + 50 &&
          event.clientY >= coinY &&
          event.clientY <= coinY + 50
        );
      });
      if (clickedCoinIndex > -1) {
        coins.splice(clickedCoinIndex, 1);
        score.value += 1;
        catSize.value += 5;
      } else {
        if(lives.value > 0) {
            lives.value -= 1;   
        }
        if(catSize.value > 55) {
            catSize.value -= 5;   
        }
      }
    };

    const startGame = () => {
      // resetGame();
      gameInterval = setInterval(() => elapsedTime.value++, 1000);
      coinInterval = setInterval(addCoin, 2000);
    };

    const resetGame = () => {
      score.value = 0;
      lives.value = 9;
      catSize.value = 50;
      coins.length = 0;
      elapsedTime.value = 0;
      clearInterval(gameInterval);
      clearInterval(coinInterval);
      startGame();
    };

    onMounted(() => startGame());
    onUnmounted(() => {
      clearInterval(gameInterval);
      clearInterval(coinInterval);
    });

    return { catImage, catStyle, coins, score, lives, isGameOver, gameArea, catchCoin, resetGame };
  },
};
</script>

