  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #f0f8ff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.coin {
  animation: fadeInOut 2s;
/*  background-image: url('');*/
  background-size: cover;
/*  width: 50px;*/
}

@keyframes fadeInOut {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: .25; }
}
